import React, { useState } from "react";
import { useNavigate } from "react-router-dom";
import axios from "axios";
import { ToastContainer, toast } from "react-toastify";
import "react-toastify/dist/ReactToastify.css";
// import { useAuth0 } from "@auth0/auth0-react";

const Login = () => {
  const [email, setemail] = useState([]);
  const [password, setpassword] = useState([]);

  const navigate = useNavigate();

  const loginfunc = async () => {
    try {
      if (email === "") return toast.error("Enter the email");
      if (password === "") return toast.error("Enter the password");
      if (password.length < 6)
        return toast.error("password length must be atleast 6");
      // else console.log("response");
      const response = await axios.post("/api/login", {
        email,
        password,
      });
      toast.info(response.data.message);
      if (response.data.success === true) {
        navigate("./home");
      }
    } catch (error) {
      console.log(error);
    }
  };

  return (
    <>
      <div
        style={{
          width: "100%",
          height: "100vh",
          backgroundColor: "#f8f9fd",
          display: "flex",
          justifyContent: "center",
          alignItems: "center",
        }}
      >
        <form
          style={{
            width: "40%",
            height: "40vh",
            backgroundColor: "#fff",
            // display: "flex",
            // justifyContent: "center",
            border: "none",
            boxShadow: "5px 7px 5px 10px #8888",
          }}
        >
          <div>
            <h1 style={{ fontSize: "2rem", padding: "20px" }}>Sign In</h1>
            <div
              class="form-group"
              style={{ paddingLeft: "20px", paddingRight: "20px" }}
            >
              <h5>Email</h5>
              <input
                type="email"
                class="form-control"
                id="exampleInputEmail1"
                aria-describedby="emailHelp"
                placeholder="Enter email"
                name="email"
                value={email}
                onChange={(e) => {
                  setemail(e.target.value);
                }}
              />
            </div>

            <div
              class="form-group"
              style={{
                fontSize: "1rem",
                paddingLeft: "20px",
                paddingRight: "20px",
              }}
            >
              <h5>Password</h5>
              <input
                type="password"
                class="form-control"
                id="exampleInputPassword1"
                placeholder="Password"
                name="password"
                value={password}
                onChange={(e) => {
                  setpassword(e.target.value);
                }}
              />
            </div>

            <button
              className="btn btn-info"
              type="button"
              style={{
                marginLeft: "300px",
                marginTop: "20px",
              }}
              onClick={() => loginfunc()}
            >
              Login
            </button>
          </div>
        </form>
      </div>
      <ToastContainer />
    </>
  );
};

export default Login;
