import React, { useState } from "react";
import Box from "@mui/material/Box";
import Input from "@mui/material/Input";
import TextField from "@mui/material/TextField";
import Button from "@mui/material/Button";
import axios from "axios";

const Aboutwriter = () => {
  const [file, setFile] = useState();
  const [name, setname] = useState("");
  const [about, setabout] = useState("");

  function handleChange(e) {
    console.log(e.target.files);
    setFile(URL.createObjectURL(e.target.files[0]));
  }

  const saveAbout = async () => {
    try {
      if (name.trim() === "") return "Please enter your name...";
      if (about.trim() === "") return "Please enter about your yourself...";
      const response = await axios.post("/api/newWriter", {
        name,
        about,
      });
      console.log(response);
      if (response.data.success === true) {
        return alert("Writer saved successfully");
      }
    } catch (error) {
      console.log(error);
    }
  };

  return (
    <>
      <div
        style={{
          backgroundImage: "url(/image/backabout.jpg)",
          backgroundRepeat: "no-repeat",
          width: "90%",
          height: "90vh",
          backgroundSize: "100% 100%",
          // color: "#faf5ef",
          // paddingTop: "100px",
          // alignSelf: "center",
          margin: "35px 70px 45px 70px",
        }}
      >
        <div className="container">
          <div
            className="row"
            style={{
              display: "flex",
              justifyContent: "space-between",
              // padding: "20px",
              // gap: "1em",
              paddingTop: "100px",
              // paddingLeft: "10px",
              // paddingRight: "100px",
            }}
          >
            {/* <div className="col-md-4">
              <h2>Add Image:</h2>
              <input
                type="file"
                multiple
                accept="image/*"
                onChange={handleChange}
              />
              <img src={file} />
            </div> */}

            <div className="col-md-8">
              <Input
                id="standard-basic fullWidth standard-size-normal"
                placeholder="Name"
                value={name}
                onChange={(e) => setname(e.target.value)}
                // inputProps={ariaLabel}
                style={{
                  // color: "#fff",
                  fontSize: "34px",
                }}
              />
              <Box
                component="form"
                sx={{
                  "& .MuiTextField-root": { mt: 5, width: "35ch" },
                }}
                noValidate
                autoComplete="off"
                style={{
                  // color: "#fff",
                  fontSize: "24px",
                }}
              >
                <TextField
                  id="standard-multiline-static"
                  label="Write about your yourself"
                  multiline
                  rows={26}
                  variant="standard"
                  value={about}
                  onChange={(e) => setabout(e.target.value)}
                />
              </Box>
            </div>
            <div className="col-md-2">
              <Button
                variant="contained"
                color="success"
                onClick={() => {
                  saveAbout();
                }}
              >
                Save
              </Button>
            </div>
          </div>
        </div>
      </div>
    </>
  );
};
export default Aboutwriter;
