import React from "react";
import { useNavigate } from "react-router-dom";
import Button from "@mui/material/Button";
import "../App.css";

const Home = () => {
  const navigate = useNavigate();
  return (
    <div>
      <center>
        <div
          style={{
            backgroundImage: "url(/image/bckgrnd.jpg)",
            backgroundRepeat: "no-repeat",
            width: "100%",
            height: "100vh",
            backgroundSize: "100% 100%",
          }}
        >
          <div
            style={{
              display: "flex",
              // justifyContent: "space-around",
              // padding: "20px",
              // gap: "1em",
              paddingTop: "100px",
              paddingLeft: "900px",
              // paddingRight: "200px",
            }}
          >
            <div
              className="col"
              // style={{
              //   paddingTop: "100px",
              // }}
            >
              <Button
                variant="contained"
                color="success"
                onClick={() => {
                  navigate("./logout");
                }}
              >
                Log Out
              </Button>
            </div>
          </div>

          <div
            style={{
              paddingTop: "130px",
              paddingLeft: "730px",
              fontSize: "10px",
            }}
          >
            <h1
              className="font-link"
              style={{
                padding: "10rem",
              }}
            >
              Share your thoughts here with us ....
            </h1>

            <Button
              variant="contained"
              color="success"
              size="large"
              style={{
                font: "style",
                size: "2rem",
                border: "3px solid #1849",
                //   backgroundColor: "",
                padding: "0.5rem 1.5rem",
                margin: "0.5rem 1.5rem",
                borderRadius: "14px",
              }}
              onClick={() => {
                navigate("./read");
              }}
            >
              Start Reading
            </Button>
            <Button
              variant="contained"
              color="success"
              size="large"
              style={{
                font: "style",
                size: "2rem",
                border: "3px solid #1849",
                //   backgroundColor: "",
                padding: "0.5rem 1.5rem",
                margin: "0.5rem 1.5rem",
                borderRadius: "14px",
              }}
              onClick={() => {
                navigate("./write");
              }}
            >
              Share Something
            </Button>
          </div>
        </div>
      </center>
    </div>
  );
};

export default Home;
