import React, { useState } from "react";
import { useNavigate } from "react-router-dom";
import { NavLink } from "react-router-dom";
import axios from "axios";
import { ToastContainer, toast } from "react-toastify";
import "react-toastify/dist/ReactToastify.css";
// import { useAuth0 } from "@auth0/auth0-react";

const Signup = () => {
  const [uname, setuname] = useState([]);
  const [email, setemail] = useState([]);
  const [password, setpassword] = useState([]);
  // const { loginWithRedirect } = useAuth0();

  const navigate = useNavigate();

  const submitData = async () => {
    try {
      if (uname === "") return toast.error("Enter the user name");
      if (email === "") {
        toast.error("email address is required");
      }
      if (!email.includes("@")) {
        toast.error("please enter valid email address");
      }
      if (password === "") return toast.error("Enter the password");
      if (password.length < 6)
        return toast.error("password length must be atleast 6");

      const response = await axios.post("/api/newuser", {
        uname,
        email,
        password,
      });
      console.log(response);
      if (response.data.success) {
        toast.success("user data submitted successfully");
        navigate("./login");
      }
    } catch (error) {
      console.log(error);
    }
  };

  return (
    <>
      <div
        style={{
          width: "100%",
          height: "100vh",
          backgroundColor: "#f8f9fd",
          display: "flex",
          justifyContent: "center",
          alignItems: "center",
        }}
      >
        <div
          style={{
            width: "60%",
            height: "58vh",
            backgroundColor: "#fff",
            // display: "flex",
            // justifyContent: "center",
            border: "none",
            boxShadow: "5px 7px 5px 10px #8888",
          }}
        >
          <h1 style={{ fontSize: "2rem", padding: "20px" }}>Sign Up</h1>
          <div
            class="form-group"
            style={{ paddingLeft: "20px", paddingRight: "20px" }}
          >
            <h5>Username</h5>
            <input
              type="text"
              class="form-control"
              id="exampleInputEmail1"
              aria-describedby="emailHelp"
              placeholder="Enter user name"
              name="uname"
              title={uname}
              onChange={(e) => {
                setuname(e.target.value);
              }}
            />
          </div>
          <div
            class="form-group"
            style={{ paddingLeft: "20px", paddingRight: "20px" }}
          >
            <h5>Email Address</h5>
            <input
              type="email"
              class="form-control"
              id="exampleInputEmail1"
              aria-describedby="emailHelp"
              placeholder="Enter email"
              name="email"
              title={email}
              onChange={(e) => {
                setemail(e.target.value);
              }}
            />
          </div>
          <div
            class="form-group"
            style={{
              fontSize: "1rem",
              paddingLeft: "20px",
              paddingRight: "20px",
            }}
          >
            <h5>Password</h5>
            <input
              type="password"
              class="form-control"
              id="exampleInputPassword1"
              placeholder="Password"
              name="Password"
              title={password}
              onChange={(e) => {
                setpassword(e.target.value);
              }}
            />
          </div>

          <button
            className="btn btn-info"
            style={{
              marginLeft: "500px",
              marginTop: "20px",
            }}
            onClick={() => submitData()}
          >
            Submit
          </button>
          <p
            style={{
              paddingLeft: "20px",
              paddingRight: "20px",
            }}
          >
            Already have an account?{" "}
            <span>
              {" "}
              <NavLink to="/login">Login</NavLink>
            </span>{" "}
          </p>
        </div>
      </div>
      <ToastContainer />
    </>
  );
};

export default Signup;
