import React from "react";
import Button from "@mui/material/Button";
import { useLocation } from "react-router-dom";
import "../App.css";
import axios from "axios";

const Viewpage = () => {
  const location = useLocation();
  console.log(location.state);
  const title = location.state.title;
  const content = location.state.content;

  const postBlog = async () => {
    try {
      if (title.trim() === "") return alert("Enter the title of your Blog !");
      if (content.trim() === "")
        return alert("Write down the content of your blog !");
      const response = await axios.post("/api/newblog", {
        title,
        content,
      });
      console.log(response);
      if (response.data.success === true) {
        return alert("Blog posted successfully!");
      }
    } catch (error) {
      console.log(error);
    }
  };

  return (
    <>
      <div
        style={{
          backgroundImage: "url(/image/viewpageBackground.jpg)",
          backgroundRepeat: "no-repeat",
          width: "99%",
          height: "99vh",
          backgroundSize: "100% 100%",
        }}
      >
        <div className="row">
          <h1
            className="col font-link"
            style={{
              paddingTop: "100px",
              paddingLeft: "800px",
              fontSize: "4rem",
              paddingBottom: "50px",
            }}
          >
            <div
              dangerouslySetInnerHTML={{
                __html: String(title),
              }}
            ></div>
          </h1>

          <div
            className="col"
            style={{
              paddingTop: "100px",
            }}
          >
            <Button
              variant="contained"
              color="success"
              onClick={() => {
                postBlog();
              }}
            >
              Post
            </Button>
          </div>
        </div>
        <center>
          <div
            dangerouslySetInnerHTML={{
              __html: String(content),
            }}
          ></div>
        </center>
      </div>
    </>
  );
};

export default Viewpage;
