import React, { useRef, useState } from "react";
import Button from "@mui/material/Button";
import Box from "@mui/material/Box";
import Input from "@mui/material/Input";
import InfoIcon from "@mui/icons-material/Info";
import { useNavigate } from "react-router-dom";
import JoditEditor from "jodit-react";
import "../App.css";
import axios from "axios";
import { useAuth0 } from "@auth0/auth0-react";
import { ToastContainer, toast } from "react-toastify";
import "react-toastify/dist/ReactToastify.css";

const ariaLabel = { "aria-label": "description" };

const BlogWrite = () => {
  const editor = useRef(null);
  const [content, setContent] = useState("");
  // const [fullcontent, setfullContent] = useState("");
  const [title, settitle] = useState("");
  const { logout } = useAuth0();
  // const [fulltitle, setfulltitle] = useState("");

  // const titleview = () => {
  //   setfulltitle(title);
  // };
  // const contentview = () => {
  //   setfullContent(content);
  // };

  const postBlog = async () => {
    try {
      if (title.trim() === "")
        return toast.error("Enter the title of your Blog !");
      if (content.trim() === "")
        return toast.error("Write down the content of your blog !");
      const response = await axios.post("/api/newblog", {
        title,
        content,
      });
      console.log(response);
      if (response.data.success === true) {
        toast.success("Blog posted successfully!");
        navigate("./read");
      }
    } catch (error) {
      console.log(error);
    }
  };

  const navigate = useNavigate();
  return (
    <>
      <div
        style={{
          backgroundImage: "url(/image/writing-background1.jpg)",
          backgroundRepeat: "no-repeat",
          width: "99%",
          height: "99vh",
          backgroundSize: "100% 100%",
        }}
      >
        <div className="row">
          <div className="col font-link">
            <h3
              style={{
                paddingTop: "100px",
                paddingLeft: "200px",
                fontSize: "32px",
              }}
            >
              Start writing your blog
            </h3>
          </div>
          <div
            style={{
              display: "flex",
              // justifyContent: "space-around",
              // padding: "20px",
              gap: "1em",
              paddingTop: "100px",
              paddingLeft: "200px",
              paddingRight: "200px",
            }}
          >
            <div
              className="col"
              // style={{
              //   paddingTop: "100px",
              // }}
            >
              <Button
                variant="contained"
                color="success"
                onClick={(e) => {
                  // titleview();
                  // contentview();
                  navigate("./viewpage", {
                    state: { title: title, content: content },
                  });
                }}
              >
                View
              </Button>
            </div>
            <div
              className="col"
              // style={{
              //   paddingTop: "100px",
              //   paddingLeft: "500px",
              // }}
            >
              <Button
                variant="contained"
                color="success"
                onClick={() => {
                  postBlog();
                }}
              >
                Post
              </Button>
            </div>
            <div
              className="col"
              // style={{
              //   paddingTop: "100px",
              //   width: "ch",
              //   height: "ch",
              //   // paddingLeft: "3px",
              // }}
            >
              <InfoIcon
                onClick={() => {
                  navigate("./aboutwriter");
                }}
              />
            </div>
            <div>
              <button
                className="col"
                onClick={() =>
                  logout({ logoutParams: { returnTo: window.location.origin } })
                }
              >
                Log Out
              </button>
            </div>
          </div>
        </div>

        <div>
          <Box
            component="form"
            sx={{
              "& > :not(style)": { mt: 10, mb: 10, width: "50ch" },
            }}
            noValidate
            autoComplete="off"
            style={{
              color: "#B3B3bf",
              fontSize: "34px",
              paddingLeft: "400px",
            }}
          >
            <Input
              id="standard-basic fullWidth standard-size-normal"
              placeholder="TITLE"
              value={title}
              onChange={(event) => {
                settitle(event.target.value);
              }}
              // inputProps={ariaLabel}
              style={{
                color: "#3b3b3b",
                fontSize: "34px",
              }}
            />
          </Box>
          {/* <h1>{fulltitle}</h1> */}
        </div>

        <div>
          <Box
            component="form"
            sx={{
              "& .MuiTextField-root": { mt: 10, width: "15ch" },
            }}
            noValidate
            autoComplete="off"
            style={{
              color: "#B3B3bf",
              fontSize: "34px",
              paddingLeft: "400px",
              paddingRight: "400px",
            }}
          >
            <JoditEditor
              style={{
                color: "#B3B3bf",
                fontSize: "34px",
                paddingLeft: "400px",
                paddingRight: "400px",
              }}
              ref={editor}
              value={content}
              onChange={(event) => {
                setContent(event);
              }}
            />
            {/* <h1>{fullcontent}</h1> */}
          </Box>
        </div>
      </div>
      <ToastContainer />
    </>
  );
};

export default BlogWrite;
