import React from "react";
import { Route, Routes, BrowserRouter as Router } from "react-router-dom";
import Home from "./Components/Home";
import BlogRead from "./Components/BlogRead";
import BlogWrite from "./Components/BlogWrite";
import Aboutwriter from "./Components/Aboutwriter";
import Viewpage from "./Components/Viewpage";
import Signup from "./Components/Signup";
import Login from "./Components/Login";
import Logout from "./Components/Logout";

function App() {
  return (
    <Router>
      <Routes>
        <Route exact path="/" element={<Signup />} />
        <Route exact path="/login" element={<Login />} />
        <Route path="/login/home" element={<Home />} />
        <Route path="/login/home/logout" element={<Logout />} />
        <Route path="/login/home/logout/signup" element={<Signup />} />
        <Route path="/login/home/read" element={<BlogRead />} />
        <Route path="/login/home/write" element={<BlogWrite />} />
        <Route path="/login/home/write/read" element={<BlogRead />} />
        <Route path="/login/home/write/aboutwriter" element={<Aboutwriter />} />
        <Route path="/login/home/write/viewpage" element={<Viewpage />} />
      </Routes>
    </Router>
  );
}

export default App;
